<template>
    <div :class="'modal-content content'+'xl'" heith="100" :updater="updater">
        <div class="modal-header">
            <button type="button" class="close" data-dismiss="modal">&times;</button>
            <h4 class="modal-title"> <i :class="modalProp.icon"></i> {{ modalProp.title }} </h4>
        </div>
        <div class="modal-body">

            <div class="row">   
                <div class="col-xs-3" >
                    <label style="text-decoration: underline;">{{$t('modals.components.labels.name')}}</label>
                </div>
                <div class="col-md-8" >
                    <label style="text-decoration: underline;">{{$t('modals.resources.labels.resource')}}</label>
                </div>
                </div>
            <div class="row" v-for="(resource, key) in resources">   
                <div class="col-xs-3" >
                        {{key}}
                </div>
                <div class="col-xs-9" >

                    <div :class="{'form-group':true, 'has-error':validation[key].err, 'has-feedback':validation[key].err}">
                        <select class="form-control" ref="res_type" :value="resource" :ref="key" @change="updateStateRes(key)">
                            <option v-for="res in filteredResources(key)"  :value="res.name">{{res.name}}</option>
                        </select>
                        <span v-if="validation[key].err" class="glyphicon glyphicon-remove form-control-feedback"></span>
                        <span v-if="validation[key].err" class="help-block">{{$t('validation.'+validation[key].msg)}}</span>
                    </div>
                    
                </div>
                <hr/>
            </div>

        </div>
        <div class="modal-footer">
            <button type="button" class="btn btn-default" data-dismiss="modal">  <i class="fa fa-times"></i> {{$t('panel.buttons.close')}}</button>
        </div>
    </div>

</template>






<script>
  import {mapActions} from 'vuex';
  import {mapGetters} from 'vuex';
  import {mapState} from 'vuex';

    export default {

      data (){
        return {
        }
      },
     computed: {
        ...mapGetters([
          'getSettings',
          'getResources'
        ]),
        ...mapState({
              currentManifest: state => state.currentManifest,
              manifests: state => state.manifests,
              resources: state => state.deploymentState.resources,
              validation: state => state.deploymentState.resValidation,
              updater: state => state.deploymentState.updater
          }),
         modalProp(){
            return this.getSettings.modalProps.resources;
        },


      },
      methods:{
        ...mapActions([
            'updateDeployResState'

        ]),

        filteredResources(key){

            let allRes = this.getResources;
            let deploy = this.manifests[this.currentManifest];
            let service = this.manifests[deploy.servicename];
            if(service){
                let resources = service.configuration.resources.filter((x)=>{ return x.name == key });
                if(resources.length==1){
                    return allRes.filter((x)=> {return x.spec == resources[0].type});
                }
            }

            return [];
        },
        updateStateRes(res_name){
            this.updateDeployResState({ key: res_name, value: this.$refs[res_name][0].value });
        }

      }
     }
</script>



