<template>
    <div class="maincontent">
        <div class="row">
            <div class="col-xs-3" >
                <div class="panel panel-default">
                        
                        <div class="panel-heading text-center">
                            Instances by Role
                        </div>
                        <!-- /.panel-heading -->
                        <div class="panel-body">
                            <donut-chart 
                                :id="charts.instances.id"
                                :data="charts.instances.data"
                                :colors="JSON.stringify(charts.instances.colors)"
                                :resize="charts.instances.resize">
                            </donut-chart>
                        </div>
                        <!-- /.panel-body -->
                    </div>
            </div>
            <div class="col-xs-3" >
                <div class="panel panel-default">
                        
                        <div class="panel-heading text-center">
                            CPU by Role
                        </div>
                        <!-- /.panel-heading -->
                        <div class="panel-body">
                            <donut-chart 
                                :id="charts.cpu.id"
                                :data="charts.cpu.data"
                                :colors="JSON.stringify(charts.cpu.colors)"
                                :resize="charts.cpu.resize">
                            </donut-chart>
                        </div>
                        <!-- /.panel-body -->
                    </div>
            </div>
            <div class="col-xs-3" >
                <div class="panel panel-default">
                        
                        <div class="panel-heading text-center">
                            Memory by Role
                        </div>
                        <!-- /.panel-heading -->
                        <div class="panel-body">
                            <donut-chart 
                                :id="charts.memory.id"
                                :data="charts.memory.data"
                                :colors="JSON.stringify(charts.memory.colors)"
                                :resize="charts.memory.resize">
                            </donut-chart>
                        </div>
                        <!-- /.panel-body -->
                    </div>
            </div>
                        <div class="col-xs-3" >
                <div class="panel panel-default">
                        
                        <div class="panel-heading text-center">
                            Resume
                        </div>
                        <!-- /.panel-heading -->
                        <div class="panel-body">
                              <bar-chart 
                                :id="charts.resume.id"
                                :data="charts.resume.data"
                                :resize="charts.resume.resize"
                                :xkey="charts.resume.xkey"
                                :ykeys = "charts.resume.ykeys"
                                :grid="charts.resume.grid"
                                >
                                
                            </bar-chart>
                        </div>
                      <!--/.panel-body -->
                    </div>
            </div>


        </div>
    </div>

</template>


<script>
  import {mapActions} from 'vuex';
  import {mapGetters} from 'vuex';
  import {mapState} from 'vuex';

    import Raphael from 'raphael/raphael'
    global.Raphael = Raphael
    import { DonutChart, BarChart } from 'vue-morris'
  
    export default {
    components:{

        DonutChart, 
        BarChart 
        // LineChart, AreaChart
      },

      data (){
        return {
        }
      },

     computed: {
        ...mapGetters([
          

        ]),
         ...mapState({
             charts: state => state.deploymentState.charts,
          }),

      },
      methods:{
          ...mapActions([
              'updateRoleName',
              'updateRoleComp',
              'changeBypass',
              'addRole',
              'updateRoleState'
        ])
      }
     }
</script>



