<template>
  <div id="control">
    <div id="control-panel" class="panel panel-default menuPannel">
      <div class="panel-heading text-center"><label> {{$t('menu.'+manifests[currentManifest].type+'.label')}}</label> </div>
      <div class="panel-body">
        <slot name="menu" ></slot>
      </div>
    </div>
     
  </div>
</template>

<script>
  import {mapActions} from 'vuex';
  import {mapGetters} from 'vuex';
  import {mapState} from 'vuex';

    export default {
      data (){
        return {
        }
      },
      computed: {
        ...mapGetters([
        ]),
          ...mapState({
              validation: state => state.serviceState.validation,
              servName: state =>  state.serviceState.name,
              updater: state => state.serviceState.updater,
              manifests: state => state.manifests,
              currentManifest: state => state.currentManifest
          })
      
      },
      methods:{
          ...mapActions([
        ]),
      
    }
}
</script>










